import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Moment from 'react-moment';
import moment from 'moment';
import { connect } from 'react-redux';
import { addLike, removeLike, deletePost } from '../../actions/post';

const PostItem = ({
  addLike,
  removeLike,
  auth,
  deletePost,
  post: { _id, text, name, avatar, user, likes, comments, date },
  showActions,
}) => (
  <div className='post bg-white p-1 my-1'>
    <div>
      <Link to={`/profile/${user}`}>
        <img className='round-img' src={avatar} alt='' />
        <h4>{name}</h4>
      </Link>
    </div>
    <div>
      <p className='my-1'>{text}</p>
      <p className='post-date'>
        Posted on <Moment format='YYYY/MM/DD'>{moment.utc(date)}</Moment>
      </p>
      {showActions && (
        <Fragment>
          <button onClick={e => addLike(_id)} type='button' className='btn btn-light'>
            <i className='fas fa-thumbs-up'></i>{' '}
            <span> {likes.length > 0 && <span>{likes.length}</span>}</span>
          </button>
          <button onClick={e => removeLike(_id)} type='button' className='btn btn-light'>
            <i className='fas fa-thumbs-down'></i>
          </button>
          <Link to={`/posts/${_id}`} className='btn btn-primary'>
            Discussion{' '}
            {comments.length > 0 && <span className='comment-count'> {comments.length}</span>}
          </Link>
          {!auth.loading && user === auth.user._id && (
            <button onClick={e => deletePost(_id)} type='button' className='btn btn-danger'>
              <i className='fas fa-times'></i>
            </button>
          )}
        </Fragment>
      )}
    </div>
  </div>
);

// https://blog.bitsrc.io/understanding-react-default-props-5c50401ed37d
// We created showActions to use PostItem component for the post/Post.js
// By default it is true and we will see content when it is true
PostItem.defaultProps = {
  showActions: true,
};

PostItem.propTypes = {
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
  addLike: PropTypes.func.isRequired,
  removeLike: PropTypes.func.isRequired,
  deletePost: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { addLike, removeLike, deletePost })(PostItem);
